@import url("https://fonts.googleapis.com/css2?family=Barlow&display=swap");

.mainpage_container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 80vh;
	width: 100vw;
	background-color: white;
}

.mainpage_wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 24px;
	margin-top: 5%;

	> .mainpage_header {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 16px;

		> .mainpage_title {
			margin: 0;
			font-family: "Barlow";
			font-style: normal;
			font-weight: 600;
			font-size: 43px;
			line-height: 56px;
			/* identical to box height, or 130% */

			text-align: center;

			/* Primary/002 */

			color: #030027;
		}
	}

	> .mainpage_body {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 24px;

		> .mainpage_button {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			padding: 12px 20px;
			gap: 12px;

			width: 158px;
			height: 44px;

			background-color: #030027;

			> a {
				color: #f9dec9;
				cursor: pointer;

				text-decoration: none;

				font-family: "Barlow";
				border-radius: 4px;
				border-color: transparent;
				line-height: 20px;
				letter-spacing: 1px;

				font-style: normal;
				font-weight: 600;
				font-size: 0.8;
			}
		}
	}
}
