@import url("https://fonts.googleapis.com/css2?family=Barlow&display=swap");

.verified_container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 80vh;
	width: 100vw;
	background-color: white;
}

.verified_wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 24px;
	margin-top: 5%;

	> .verified_header {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 16px;

		> h1 {
			margin: 0;
			font-family: "Barlow";
			font-style: normal;
			font-weight: 600;
			font-size: 43px;
			line-height: 56px;
			/* identical to box height, or 130% */

			text-align: center;

			/* Primary/002 */

			color: #030027;
		}
	}

	> .verified_body {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 24px;

		> p {
			margin: 0;
			/* Subtitle 1/Regular */

			font-family: "Barlow";
			font-style: normal;
			font-weight: 400;
			font-size: 19px;
			line-height: 24px;
			/* identical to box height, or 126% */

			text-align: center;
			letter-spacing: 0.2px;

			/* Dark */

			color: #13101a;
		}

		button {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			padding: 12px 20px;
			gap: 12px;

			width: 217px;
			height: 44px;

			/* Primary/002/Normal */

			background: #030027;
			border-radius: 4px;

			font-family: "Barlow";
			font-style: normal;
			font-weight: 600;
			font-size: 14px;
			line-height: 20px;
			/* identical to box height, or 143% */

			letter-spacing: 1px;

			/* Primary/ 001 */

			color: #f9dec9;
		}
	}
}
